

import React from 'react'
import axios from 'axios'
import { Layout, Menu, Breadcrumb } from 'antd';
import { Select } from 'antd';
import { Link } from 'react-router-dom';
// 引入 ECharts 主模块
import echarts from 'echarts/lib/echarts';
// 引入折线图
import 'echarts/lib/chart/line'
// 引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';

const { Header, Content, Footer } = Layout;
const Option = Select.Option;


class Performs extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            IpArray: [],
            performs: {
                category: [],
                memData: [],
                diskData: [],
                cpuData: []
            }
        }
    }

    componentDidMount = () => {
        this.getPerformsIp();
        this.initPerformsEcharts();
    }

    componentDidUpdate = () => {
        this.initPerformsEcharts();
    }

    initPerformsEcharts = () => {
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('main'));
        // 绘制图表
        myChart.setOption({
            title: {
                text: '主机性能曲线图'
            },
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['内存使用率', '磁盘使用率', 'CPU使用率']
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            toolbox: {
                feature: {
                    saveAsImage: {}
                }
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: this.state.performs.category
            },
            yAxis: {
                type: 'value',
                max: 100
            },
            series: [
                {
                    name: '内存使用率',
                    type: 'line',
                    data: this.state.performs.memData
                },
                {
                    name: '磁盘使用率',
                    type: 'line',
                    data: this.state.performs.diskData
                },
                {
                    name: 'CPU使用率',
                    type: 'line',
                    data: this.state.performs.cpuData
                },
            ]
        });
    }

    getPerformsIp = () => {
        axios.get('/t/api/get_host_performs_ip').then((res) => {
            if (res.data.result) {
                this.setState({
                    IpArray: res.data.data
                })
            }
            console.log('ip:', this.state.IpArray)
        })
    }

    getPerformsHistory = (ip) => {
        axios.get('/t/api/get_host_performs_history', {
            params: {
                ip: ip ? ip : null
            }
        }).then((res) => {
            console.log(res);
            if (res.data.result) {
                let category = [];
                let memData = [];
                let diskData = [];
                let cpuData = [];
                console.log('aaa:', res);
                res.data.data.map((item) => {
                    category.push(item.create_time);
                    memData.push(parseFloat(item.mem.replace(/[^\d.]/g, '')));
                    diskData.push(parseFloat(item.disk.replace(/[^\d.]/g, '')));
                    cpuData.push(parseFloat(item.cpu.replace(/[^\d.]/g, '')));
                })
                console.log('category:', category);
                console.log('mem:', memData);
                console.log('disk', diskData);
                console.log('cpu:', cpuData);
                this.setState({
                    performs: {
                        category: category,
                        memData: memData,
                        diskData: diskData,
                        cpuData: cpuData
                    }
                })
                console.log('performs:',this.state.performs);
            }
        })
    }

    handlePerforms = (value) => {
        this.getPerformsHistory(value);
    }

    render() {
        return (

            <div>
                <Layout className="layout">
                    <Header>
                        <div className="logo" />
                        <Menu
                            theme="dark"
                            mode="horizontal"
                            defaultSelectedKeys={['2']}
                            style={{ lineHeight: '64px' }}
                        >
                            <Menu.Item key="1">
                                <Link to="/dist/">主机查询</Link>
                            </Menu.Item>
                            <Menu.Item key="2">
                                <Link to="#">性能监控</Link>
                            </Menu.Item>
                        </Menu>
                    </Header>
                    <Content style={{ padding: '0 50px' }}>
                        <div style={{ margin: '20px auto', maxWidth: '400px' }}>
                            <div style={{ textAlign: 'left' }}>
                                <span style={{ marginRight: '10px' }}>
                                    请选择主机:
                                </span>
                                <Select
                                    showSearch
                                    style={{ width: 200 }}
                                    placeholder="请选择一个主机进行展示"
                                    optionFilterProp="children"
                                    onChange={this.handlePerforms}
                                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                >
                                    {
                                        this.state.IpArray.map((item, index) => {
                                            return <Option key={index} value={item}>{item}</Option>
                                        })
                                    }
                                </Select>
                            </div>
                        </div>
                        <div id="main" style={{ margin: '20px auto', width: 1000, height: 400 }}></div>
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>
                        Ant Design ©2016 Created by Ant UED
                    </Footer>
                </Layout>

            </div >
        )
    }
}

export default Performs;