import React from 'react'
import axios from 'axios'
import { Table, Icon, Button, Select, Input } from 'antd';
import { Layout, Menu, Breadcrumb } from 'antd';
import { Popconfirm, message } from 'antd';
import { Link } from 'react-router-dom'
import { Row, Col } from 'antd';
const { Header, Content, Footer } = Layout;
const { TextArea } = Input;


const Option = Select.Option;

class Business extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ipList: "",
            ServicesArray: [],
            performsIp: []
        }

        this.columns = [{
            title: '内网IP',
            dataIndex: 'IP',
            key: 'IP',
            render: text => <a href="#">{text}</a>,
        }, {
            title: '系统名',
            dataIndex: 'SystemName',
            key: 'SystemName',
        }, {
            title: '主机名',
            dataIndex: 'HostName',
            key: 'HostName',
        }, {
            title: '云区域',
            dataIndex: 'Zone',
            key: 'Zone',
        }, {
            title: 'Mem(%)',
            dataIndex: 'Mem',
            key: 'Mem',
        }, {
            title: 'Disk(%)',
            dataIndex: 'Disk',
            key: 'Disk',
        }, {
            title: 'CPU(%)',
            dataIndex: 'CPU',
            key: 'CPU',
        }, {
            title: '操作',
            key: 'action',
            render: (text, record) => (
                <span>
                    <Popconfirm
                        title="是否查询系统使用资源"
                        onConfirm={ () => this.handlePopConfirm(record)}
                        onCancel={ () =>　this.handlePopCancel()}
                        okText="OK"
                        cancelText="Cancel"
                    >
                        <a href="#"><Icon type="zoom-in" /></a>
                    </Popconfirm>
                    <span className="ant-divider" />
                    <a href="#" onClick={ () => record.isMonitor ? this.handleRemoveFromMonitor(record) : this.handleAddToMonitor(record)}>
                        <Icon type={record.isMonitor ? "delete" : "monitor"} />
                    </a>
                </span>
            ),
        }];
    }

    componentDidMount = () => {
        this.getPerformsIp();
        // 获取业务信息
        axios.post('/t/api/get_business_info', {
            "fields": [
                "bk_biz_id",
                "bk_biz_name"
            ]
        })
            .then((res) => {
                console.log(res);
                this.setState({
                    ServicesArray: res.data.data.info
                });
                console.log('aaa:', this.state.ServicesArray);
            })
            .catch(function (error) {
                console.log(error);
            });

        console.log(this.ServicesArray);

    };

    handleServices = (value) => {
        this.setState({
            selectBusiness: value
        })
        console.log(`selected ${value}`);
        // 选择业务后触发获取业务主机信息
        this.getServicesHost(value);
    }

    // 获取业务的主机信息
    getServicesHost = async (bk_biz_id, ip) => {
        await this.getPerformsIp();
        console.log('performsIp-host:', this.state.performsIp);

        axios.post('/t/api/get_host_info', {
            bk_biz_id: bk_biz_id,
            ip: {
                data: ip ? ip : [],
                exact: 0,
                flag: "bk_host_innerip"
            },
        })
            .then((res) => {
                const HostArray = [];
                for (let i = 0; i < res.data.data.count; i++) {
                    let item = res.data.data.info[i].host;
                    HostArray.push({
                        key: item.bk_host_id,
                        IP: item.bk_host_innerip,
                        SystemName: item.bk_os_name,
                        HostName: item.bk_host_name,
                        bk_cloud_id: item.bk_cloud_id ? item.bk_cloud_id[0].id : null,
                        Zone: item.bk_cloud_id ? item.bk_cloud_id[0].bk_inst_name : null,
                        Mem: '--',
                        Disk: '--',
                        CPU: '--',
                        isMonitor: this.state.performsIp.includes(item.bk_host_innerip)
                    })
                    console.log('s-p:',  this.state.performsIp);
                    console.log('i-ip:', item.bk_host_innerip);
                }
                this.setState({
                    HostArray: HostArray
                })
                console.log('hostArray:',HostArray);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    handleHost = () => {
        const ip = this.state.ipList.length > 0 ? this.state.ipList.split('\n') : null;
        console.log('ip：', ip);
        this.getServicesHost(this.state.selectBusiness, ip)
        console.log('value:', this.state.ipList);
    }

    // 查询主机的性能数据(内存、磁盘、CPU使用率)
    handleHostPerforms = (row) => {
        axios.post('/t/api/get_host_performs', {
                bk_biz_id: row.bk_biz_id,
                script_content: '',
                script_timeout: 1000,
                account: 'root',
                script_type: 1,
                ip_list: [
                    {
                        bk_cloud_id: row.bk_cloud_id,
                        ip: row.IP
                    }
                ]
        }).then((res) => {
            if (res.data.result) {
                console.log('run_performs:', res);
                const results = res.data.data[0].step_results[0].ip_logs;
                const logs = results.map((item) => {
                    return {
                        bk_cloud_id: item.bk_cloud_id,
                        ip: item.ip,
                        log_content: item.log_content
                    }
                })
                // 分割内容
                const performs = logs.map((item) => {
                    const performs = item.log_content.split('|');
                    return {
                        Date: performs[0],
                        Mem: performs[1],
                        Disk: performs[2],
                        CPU: performs[3]
                    }
                });
                console.log('performs:', performs);
                // 更新表格内容
                const newData = [...this.state.HostArray];
                const index = newData.findIndex(item => row.key == item.key);
                newData[index].Mem = performs[0].Mem;
                newData[index].Disk = performs[0].Disk;
                newData[index].CPU = performs[0].CPU;
                this.setState({ HostArray: newData });
            }
        })
    }

    handleIpList = ({ target: { value }} ) => {
        this.setState({
            ipList: value
        })
    }

    handlePopConfirm = (record, e) => {
        this.handleHostPerforms(record);
      }
      
    handlePopCancel = (e) => {
        message.error('取消查询系统使用资源');
    }

    // 将主机加入性能监控
    handleAddToMonitor = (record) => {
        axios.get('/t/api/add_host_performs', {
            params: {
                ip: record.IP,
                bk_biz_id: this.state.selectBusiness,
                bk_cloud_id: record.bk_cloud_id
            }
        }).then((res) => {
            console.log(res);
            // 更新表格内容
            const newData = [...this.state.HostArray];
            const index = newData.findIndex(item => record.key == item.key);
            newData[index].isMonitor = true;
            this.setState({ HostArray: newData });
        })
    }

    // 将主机从性能监控中移除
    handleRemoveFromMonitor = (record) => {
        console.log('remove');
        axios.get('/t/api/remove_host_performs', {
            params: {
                ip: record.IP,
            }
        }).then((res) => {
            console.log(res);
            // 更新表格内容
            const newData = [...this.state.HostArray];
            const index = newData.findIndex(item => record.key == item.key);
            newData[index].isMonitor = false;
            this.setState({ HostArray: newData });
        })
    }

    // 获取正在监控的主机IP
    getPerformsIp = () => {
        axios.get('/t/api/get_host_performs_ip').then((res) => {
            if (res.data.result) {
                this.setState({
                    performsIp: res.data.data
                })
            }
            console.log('ip-p:', this.state.performsIp)
        })
    }

    render() {
        return (
            <div>
                <Layout className="layout">
                    <Header>
                        <div className="logo" />
                        <Menu
                            theme="dark"
                            mode="horizontal"
                            defaultSelectedKeys={['1']}
                            style={{ lineHeight: '64px' }}
                        >
                            <Menu.Item key="1">
                                <Link to="#">主机查询</Link>
                            </Menu.Item>
                            <Menu.Item key="2">
                                <Link to="/dist/performs">性能监控</Link>
                            </Menu.Item>
                        </Menu>
                    </Header>
                    <Content style={{ padding: '0 50px' }}>
                        <div style={{ margin: '20px auto', maxWidth: '400px' }}>
                            <Row type="flex" justify="start">
                                <Col span={6} style={{textAlign: 'left'}}>
                                    <span style={{ marginRight: '10px'}}>
                                        请选择业务:
                                    </span>
                                </Col>
                                <Col span={18}>
                                    <Select
                                        showSearch
                                        style={{ width: '100%' }}
                                        placeholder="选择一个业务"
                                        optionFilterProp="children"
                                        onChange={this.handleServices}
                                        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                    >
                                        {
                                            this.state.ServicesArray.map((item) => {
                                                return <Option key={item.bk_biz_id} value={item.bk_biz_id}>{item.bk_biz_name}</Option>
                                            })
                                        }
                                    </Select>
                                </Col>
                            </Row>
                            <Row type="flex" justify="start" style={{marginTop:'10px'}}>
                                <Col span={6} style={{textAlign: 'left'}}>
                                    <span style={{ marginRight: '10px'}}>
                                        请输入主机IP:
                                    </span>
                                </Col>
                                <Col span={18}>
                                    <TextArea
                                        onChange={this.handleIpList}
                                        placeholder="请输入主机IP,多个IP用回车分割"
                                        autosize={{ minRows: 2, maxRows: 6 }}
                                    />
                                </Col>
                            </Row>
                            <div style={{ margin: '8px 0' }}>
                                <Button type="primary" icon="search" onClick={this.handleHost}>查询</Button>
                            </div>
                        </div>
                        <Table columns={this.columns} dataSource={this.state.HostArray} />;
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>
                        Ant Design ©2016 Created by Ant UED
                    </Footer>
                </Layout>

            </div >
        )

    }
}

export default Business;

